@import url(//cdn.bootcss.com/bootstrap/3.3.5/css/bootstrap.min.css);
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}


* {
	margin: 0;
	padding: 0;
}
html {
	height: 100%;
}
body {
	height: 100%;
	background: #fff url(/static/img/backgroud.21491d4.png) 50% 50% no-repeat;
	background-size: cover;
}
.dowebok {
	position: absolute;
	left: 50%;
	top: 50%;
	width: 430px;
	height: 550px;
	margin: -300px 0 0 -215px;
	border: 1px solid #fff;
	border-radius: 20px;
	overflow: hidden;
}
.logo {
	width: 104px;
	height: 104px;
	margin: 50px auto 80px;
	background: url(data:image/png;base64,) 0 0 no-repeat;
}
.form-item-login {
	position: relative;
	width: 360px;
	margin: 0 auto;
	padding-bottom: 30px;
}
.form-item-login input {
	width: 300px;
	height: 48px;
	padding-left: 70px;
	border: 1px solid #fff;
	border-radius: 25px;
	font-size: 18px;
	color: #fff;
	background-color: transparent;
	outline: none;
}
.form-item-login button {
	width: 360px;
	height: 50px;
	border: 0;
	border-radius: 25px;
	font-size: 18px;
	color: #1f6f4a;
	outline: none;
	cursor: pointer;
	background-color: #fff;
}
#username {
	background: url(data:image/png;base64,) 20px 14px no-repeat;
}
#password {
	background: url(data:image/png;base64,) 23px 11px no-repeat;
}
#myAlert2 {
	display: none;
}
.tipEmail {
	display: none;
	position: absolute;
	left: 20px;
	top: 52px;
	font-size: 14px;
	color: #f50;
}
.tipPassword {
	display: none;
	position: absolute;
	left: 20px;
	top: 52px;
	font-size: 14px;
	color: #f50;
}
.reg-bar {
	width: 360px;
	margin: 20px auto 0;
	font-size: 14px;
	overflow: hidden;
}
.reg-bar a {
	color: #fff;
	text-decoration: none;
}
.reg-bar a:hover {
	text-decoration: underline;
}
.reg-bar .reg {
	float: left;
}
.reg-bar .forget {
	float: right;
}
.dowebok ::-webkit-input-placeholder {
	font-size: 18px;
	line-height: 1.4;
	color: #fff;
}
.dowebok :-moz-placeholder {
	font-size: 18px;
	line-height: 1.4;
	color: #fff;
}
.dowebok ::-moz-placeholder {
	font-size: 18px;
	line-height: 1.4;
	color: #fff;
}
.dowebok :-ms-input-placeholder {
	font-size: 18px;
	line-height: 1.4;
	color: #fff;
}
@media screen and (max-width: 500px) {
* {
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
}
.dowebok {
		position: static;
		width: auto;
		height: auto;
		margin: 0 30px;
		border: 0;
		border-radius: 0;
}
.logo {
		margin: 50px auto;
}
.form-item-login {
		width: auto;
}
.form-item-login input,
	.form-item-login button,
	.reg-bar {
		width: 100%;
}
}

